import React, { Component } from "react";
import reactlogo from '../image/react.png';
import htmllogo from '../image/html.png';
import csslogo from '../image/css.svg';
import jslogo from '../image/js.png';
import gitlogo from '../image/github.svg';
import adobexd from '../image/adobexd.svg';
import api from '../image/api.svg';
import responsive from '../image/responsive.svg';

export default class About extends Component {
  render() {
    return (
      <div className="about" id="about">
      <h1 className="heading heading--black">My skills</h1>
        <div className="skills">
          <div className="hover">
            <img className="skill" src={reactlogo} alt="react-logo" />
            <span className="hover__text">
              Reactjs, my go-to Javascript framework.
            </span>
          </div>
          <div className="hover">
            <img className="skill" src={htmllogo} alt="react-logo" />
            <span className="hover__text">HTML</span>
          </div>
          <div className="hover">
            <img className="skill" src={csslogo} alt="react-logo" />
            <span className="hover__text">Css (Scss), flexbox and grid.</span>
          </div>
          <div className="hover">
            <img className="skill" src={jslogo} alt="react-logo" />
            <span className="hover__text">Javascript, ES6, Regex etc.</span>
          </div>
        </div>
        <div className="skills">
          <div className="hover">
            <img className="skill" src={gitlogo} alt="react-logo" />
            <span className="hover__text">
              Reactjs, my go-to Javascript framework.
            </span>
          </div>
          <div className="hover">
            <img className="skill" src={api} alt="react-logo" />
            <span className="hover__text">HTML</span>
          </div>
          <div className="hover">
            <img className="skill" src={adobexd} alt="react-logo" />
            <span className="hover__text">Css (Scss), flexbox and grid.</span>
          </div>
          <div className="hover">
            <img className="skill" src={responsive} alt="react-logo" />
            <span className="hover__text">Javascript, ES6, Regex etc.</span>
          </div>
        </div>
      </div>
    );
  }
}



