import React, { Component } from "react";

export default class Contact extends Component {
  render() {
    return (
      <div className="contact">
        <div className="contact__me">
          <div className="text-box--margin-top-bottom">
            <h1 className="description description--grey description--small">
              I am a student who's fascinated by the world of computer science.
              In 2018 Spring quarter, I took a computer science class and since
              been hooked. I commited to the{" "}
              <a href="https://www.100daysofcode.com/">#100DaysOfCode</a>
              challenge 2 months ago, and has been coding extensive hours every
              day.
            </h1>
          </div>
        </div>
        <div className="contact__social">
          <div className="text-box">
            <div className="icon-wrapper">
              <a
                href="https://facebook.com/jamesaubudi"
                className="fa fa-facebook"
                aria-hidden="true"
              />
              <span className="caption">@jamesaubudi</span>
            </div>
            <div className="icon-wrapper">
              <a
                href="https://twitter.com/JamesAuCode"
                className="fa fa-twitter"
                aria-hidden="true"
              />
              <span className="caption">@jamesaucode</span>
            </div>
            <div className="icon-wrapper">
              <a
                href="https://github.com/jamesaucode"
                className="fa fa-github"
                aria-hidden="true"
              />
              <span className="caption">@jamesaucode</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



